.container {
  display: grid;
  grid-template-columns: repeat(1, minmax(200px, 400px));
  gap: 30px;
  padding: 50px 60px;
  width: max-content;
  height: max-content;
  border-radius: 20px;
  background: #fff;
  box-shadow: 0px 0px 50px 20px rgb(121, 116, 221, 0.1);
}
.title {
  color: var(--main-color);
  font-size: 30px;
  font-weight: 500;
  line-height: 114%;
}